.page {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  padding: 10px 0;
  display: block;
}

.nav__list {
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  height: 100%;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 100%;
  text-decoration: none;
  transition: color .3s;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  transition-property: transform, color;
  transition-duration: .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__top {
  justify-content: center;
  margin: 32px 0 40px;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
}

.card__content {
  flex-direction: column;
  padding: 0 16px 16px;
  display: flex;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 1.4;
}

.card__rating {
  margin-bottom: 24px;
}

.card__rating, .card__cost {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__cost {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__reviews {
  font-size: 10px;
  font-weight: 400;
  line-height: 1.4;
}

.card__price {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__purchase-link {
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  padding: 12px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition-property: background-color, color;
  transition-duration: .3s;
  display: inline;
}

.card__purchase-link:hover {
  color: #00acdc;
  background-color: #000;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

body, p {
  margin: 0;
}

.main__catalog {
  padding: 50px 0;
}

